#img-section{
  box-shadow:
  0 20px 25px -5px rgba(0,0,0, .1),
  0 10px 15px -5px rgba(0,0,0, .1),
  10px 0 15px -5px rgba(0,0,0, .1),
  -10px 0 15px -5px rgba(0,0,0, .1);
  border-radius: 25px;
}
.bg{
position: relative;
transition: all 0.3s;
border-radius: 25px;
height: 100%;
}

#SVG{
  border-radius: 25px;
}
.image{
  max-width: 900px;
  max-height: 900px;
  cursor: pointer;

  position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);

  border-radius: 25px;
  box-shadow:
  0 20px 25px -5px rgba(0,0,0, .4),
  0 10px 15px -5px rgba(0,0,0, .3),
  10px 0 15px -5px rgba(0,0,0, .4),
  -10px 0 15px -5px rgba(0,0,0, .4);
  overflow: hidden;

}

.start-msg{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  background-color: #851e3e;
  width: 390px; height: 200px;
  color: rgb(219, 219, 219);
  border-radius: 25px;
  padding: 5px 23px;
  display: flex;
  flex-direction: column;
  font-family: 'Roboto', sans-serif;
  cursor: pointer;
  align-content: center;
}

.greeting h1{
  text-align: center;
  font-size: 2rem;
  margin-bottom: 5px;
}

.start-about{
  font-size: 1.3rem;
  margin-top: 15px;
}

.get-started{
  margin-top: 30px;
  text-align: center;
}