@import url(https://fonts.googleapis.com/css2?family=Hammersmith+One&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::before, *::after {
  box-sizing: border-box;
}

body{
  background-color: #393E46;
}


canvas{
  display: none;
}

canvas img{
  display: none;
}
.patterns-scroll::-webkit-scrollbar{
  background-color: #939597 !important;
}
.scroll::-webkit-scrollbar{
  width: 12px;
  background-color: #393E46;
  border-radius: 25px;
}

.scroll::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	/* -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3); */
	background-color: #e4e4e4;
}


.project-title{
  color: #F5DF4D;
  text-align: center;
  margin-top: 20px;
  letter-spacing: 5px;
  font-size: 1.5rem;
  font-family: 'Hammersmith One', sans-serif;
}
.pattern-btn{
  min-width: 200px;
  min-height: 120px;
  outline: none;
  border: none;
  border-radius: 15px;
  cursor: "pointer";
  margin: 0 15px 15px;
  background-position: center center;
  background-repeat: repeat;
  cursor: pointer;
  /* transition: all 0.4s; */
}
.background{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
}
.patterns-container{
  width: 90%;
  max-width: 1400px;
}
.patterns-btns{
  margin-top: 15px;
  display: flex;
  min-width: 190px;
  overflow-x: auto;
  display: flex;
  overflow-x: scroll;
  flex-wrap: nowrap;
  -webkit-overflow-scrolling: touch;

  overflow-y: hidden;
    white-space: nowrap;
    flex-wrap: nowrap;
}

.active{
  margin: auto;
  transition: morphing 0.1s;
  
	/* background-color: #21D4FD; */
	/* background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%); */
  border-radius: 30% 70% 70% 50% / 30% 30% 70% 70%;
	/* box-shadow: 15px 15px 50px rgba(0,0,0,0.2); */
	animation: morphing 10s infinite;
	overflow: hidden;}
	

  @keyframes morphing {
		0% {
      /* border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%; */
      border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
      /* box-shadow: 15px 15px 50px rgba(0,0,0,0.2); */
		}
		25% { 
      border-radius: 58% 42% 75% 25% / 76% 46% 54% 24%;
      
		}
		50% {
			border-radius: 50% 50% 33% 67% / 55% 27% 73% 45%;
      /* box-shadow: -10px -15px 10px rgba(0,0,0,0.1); */
      
		}
		75% {
      border-radius: 33% 67% 58% 42% / 63% 68% 32% 37%;
    }
    100%{
      border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    }
	}
#img-section{
  box-shadow:
  0 20px 25px -5px rgba(0,0,0, .1),
  0 10px 15px -5px rgba(0,0,0, .1),
  10px 0 15px -5px rgba(0,0,0, .1),
  -10px 0 15px -5px rgba(0,0,0, .1);
  border-radius: 25px;
}
.bg{
position: relative;
transition: all 0.3s;
border-radius: 25px;
height: 100%;
}

#SVG{
  border-radius: 25px;
}
.image{
  max-width: 900px;
  max-height: 900px;
  cursor: pointer;

  position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);

  border-radius: 25px;
  box-shadow:
  0 20px 25px -5px rgba(0,0,0, .4),
  0 10px 15px -5px rgba(0,0,0, .3),
  10px 0 15px -5px rgba(0,0,0, .4),
  -10px 0 15px -5px rgba(0,0,0, .4);
  overflow: hidden;

}

.start-msg{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  background-color: #851e3e;
  width: 390px; height: 200px;
  color: rgb(219, 219, 219);
  border-radius: 25px;
  padding: 5px 23px;
  display: flex;
  flex-direction: column;
  font-family: 'Roboto', sans-serif;
  cursor: pointer;
  align-content: center;
}

.greeting h1{
  text-align: center;
  font-size: 2rem;
  margin-bottom: 5px;
}

.start-about{
  font-size: 1.3rem;
  margin-top: 15px;
}

.get-started{
  margin-top: 30px;
  text-align: center;
}
.background-color-btn{
  outline: none;
  border: none;
  margin: 5px;
  min-width: 80px;
  min-height: 80px;
  cursor: pointer;
  border-radius: 25px;
}

.foreground-color-btn{
  outline: none;
  border: none;
  margin: 5px;
  min-width: 80px;
  min-height: 80px;
  border-radius: 25px;
  cursor: pointer;
}

.colors{
  background: #939597;
  padding: 40px;
  margin: 30px 0 100px;
  width: 90%;
  max-width: 1400px;
  overflow: hidden;
  border-radius: 25px;
  box-shadow:
  0 20px 25px -5px rgba(0,0,0, .2),
  0 10px 15px -5px rgba(0,0,0, .1),
  10px 0 15px -5px rgba(0,0,0, .2),
  -10px 0 15px -5px rgba(0,0,0, .2);
}

.background-colors-container{
  display: flex;
  overflow-x: scroll;
  flex-wrap: nowrap;
margin: 10px 0px 30px;
}


.foreground-colors-container{
  display: flex;
  overflow-x: auto;
  flex-wrap: nowrap;
  /* -webkit-overflow-scrolling: touch; */

}

.download-btn{
  margin: 40px 0;
}

.download-btn button{
  cursor: pointer;

  padding: .9rem 1.7rem;
  color: #1e1e1e;
  font-weight: 500;
  font-size: 16px;
  transition: all 0.3s ease-in-out;
  background: #F5DF4D;
  border: solid 1px #1e1e1e;
  box-shadow: inset 0 0 0 2px #FFF;
  margin-right: 1em;
}

.download-btn button:hover {
  text-decoration: underline;
  color: rgb(219, 219, 219);
  background:#851e3e;
  box-shadow: inset 0 0 0 4px #FFF;
}
