.pattern-btn{
  min-width: 200px;
  min-height: 120px;
  outline: none;
  border: none;
  border-radius: 15px;
  cursor: "pointer";
  margin: 0 15px 15px;
  background-position: center center;
  background-repeat: repeat;
  cursor: pointer;
  /* transition: all 0.4s; */
}
.background{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
}
.patterns-container{
  width: 90%;
  max-width: 1400px;
}
.patterns-btns{
  margin-top: 15px;
  display: flex;
  min-width: 190px;
  overflow-x: auto;
  display: flex;
  overflow-x: scroll;
  flex-wrap: nowrap;
  -webkit-overflow-scrolling: touch;

  overflow-y: hidden;
    white-space: nowrap;
    flex-wrap: nowrap;
}

.active{
  margin: auto;
  transition: morphing 0.1s;
  
	/* background-color: #21D4FD; */
	/* background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%); */
  border-radius: 30% 70% 70% 50% / 30% 30% 70% 70%;
	/* box-shadow: 15px 15px 50px rgba(0,0,0,0.2); */
	animation: morphing 10s infinite;
	overflow: hidden;}
	

  @keyframes morphing {
		0% {
      /* border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%; */
      border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
      /* box-shadow: 15px 15px 50px rgba(0,0,0,0.2); */
		}
		25% { 
      border-radius: 58% 42% 75% 25% / 76% 46% 54% 24%;
      
		}
		50% {
			border-radius: 50% 50% 33% 67% / 55% 27% 73% 45%;
      /* box-shadow: -10px -15px 10px rgba(0,0,0,0.1); */
      
		}
		75% {
      border-radius: 33% 67% 58% 42% / 63% 68% 32% 37%;
    }
    100%{
      border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    }
	}