.download-btn{
  margin: 40px 0;
}

.download-btn button{
  cursor: pointer;

  padding: .9rem 1.7rem;
  color: #1e1e1e;
  font-weight: 500;
  font-size: 16px;
  transition: all 0.3s ease-in-out;
  background: #F5DF4D;
  border: solid 1px #1e1e1e;
  box-shadow: inset 0 0 0 2px #FFF;
  margin-right: 1em;
}

.download-btn button:hover {
  text-decoration: underline;
  color: rgb(219, 219, 219);
  background:#851e3e;
  box-shadow: inset 0 0 0 4px #FFF;
}